import { useState } from 'react'
import { Text, useTick } from '@inlet/react-pixi'
import { TextStyle } from 'pixi.js';
function RotatingText() {
    const [dragging, setDragging] = useState(false)
    const [x, setX] = useState(120)
    const [y, setY] = useState(120)
    const [diffX, setDiffX] = useState(0)
    const [diffY, setDiffY] = useState(0)  

    const [rotation, setRotation] = useState(0)
    useTick(delta => {
        setRotation(rotation + 0.01 * delta)
    })

    const mouseDown = (e) => {
      let distanceX = e.data.global.x - x;
      let distanceY = e.data.global.y - y;
      setDiffX(distanceX)
      setDiffY(distanceY)
      setDragging(true)
      setX(e.data.global.x - distanceX)
      setY(e.data.global.y - distanceY)
    }
  
    const mouseMove = (e) => {
      if(dragging){
        setX(e.data.global.x - diffX)
        setY(e.data.global.y - diffY)
      }
    }
  
    const mouseUp = () => {
      setDragging(false)
    }
    
    return (
      <Text text=""
        x={x} 
        y={y}
        interactive={true}
        style={new TextStyle({fill:'white'})}
        rotation={rotation}
        anchor={0.5}
        // mousedown={mouseDown}
        // mousemove={mouseMove}
        // mouseup={mouseUp}
    />           
    )
  }

export default RotatingText