import { useState } from 'react'
import { Graphics, useTick } from '@inlet/react-pixi'

function Ball(){
    const [props,  setProps] = useState({
        x: 150,
        y: 20,
        size: 5,
        color: 0xff0000,
    });
    useTick(delta => {
        setProps({...props, y: props.y + 1 * delta});
    })

    const mouseMove = (e) => {
        // console.log('pad: ' + params.x);
    }

    return (
        <Graphics draw={g => {
            g.clear();
            g.beginFill(props.color);
            g.lineStyle(1, 0xff0000, 0.5)
            g.drawCircle(props.x, props.y, props.size);
            g.endFill();
        }}
        // interactive
        // mousemove={mouseMove}
        />
    )
}
export default Ball;
