import { useState } from 'react'
import { Graphics } from '@inlet/react-pixi'
import { Rectangle } from 'pixi.js';

function Paddle(){
    const [props,  setProps] = useState({
        x: 250,
        y: 450,
        width: 80,
        height: 10,
        color: 0xffffff,
    });

    const rect = new Rectangle(props.x, props.y, props.width, props.height);


    const mouseMove = (e) => {
        setProps({...props, x: e.data.global.x - props.width / 2});
        // console.log('pad: ' + params.x);
    }

    return (
        <Graphics draw={g => {
            g.clear();
            g.beginFill(props.color);
            g.drawRect(props.x, props.y, props.width, props.height);
            g.endFill();
        }}
        interactive
        mousemove={mouseMove}
        />

        // <Sprite
        //     image="https://s3-us-west-2.amazonaws.com/s.cdpn.io/693612/coin.png"
        //     width={props.width}
        //     height={props.height}
        //     x={props.x}
        //     y={props.y}
        //     interactive={true}
        //     anchor={0.5}
        //     mousemove={mouseMove}
        //     />
    )
}
export default Paddle;

